.list-view {
    display: flex;
    flex-direction: column;
}

.list-view .card {
    background-color: #f5f5f6;
    display: flex;
    align-items: center;
    padding-right: 20px;
    justify-content: space-between;
    margin-bottom: 3px;
}

.list-view .card__img {
    width: 120px;
}